<template>
  <v-container fluid grid-list-md>

    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-toolbar color="light-blue" dark>
            <v-toolbar-side-icon></v-toolbar-side-icon>
            <v-toolbar-title>Group list</v-toolbar-title>
            <v-spacer></v-spacer>
            <!-- <SearchGroup /> -->
            <!-- <v-btn icon v-model="search" >
              <v-icon>search</v-icon>
            </v-btn> -->
          </v-toolbar>
          <v-toolbar color="light-blue" dark>
            <!-- <v-autocomplete
              :items="groups"
              color="white"
              item-text="groupName"
              label="ค้นหาชื่อกลุ่ม"
                            
            ></v-autocomplete> -->
            <v-text-field 
              type="text" 
              v-model="search" 
              placeholder="ค้นหาชื่อกลุ่ม">
            </v-text-field>
          </v-toolbar>

          <v-list  three-line>
            <v-list-tile
              v-for="group in filteredGroup"
              :key="group.id"
              avatar
              :to="{name: 'view-group', params: {groupId: group.groupId}}"
            >
              <v-list-tile-content>
                <v-list-tile-title>{{ group.groupName }}  {{group.groupId}}</v-list-tile-title>
                <v-list-tile-sub-title>มีสมาชิก:{{ group.groupMember }}</v-list-tile-sub-title>
                <v-list-tile-sub-title>ผู้ประสานงาน:{{ group.coordinator }}</v-list-tile-sub-title>
                <v-list-tile-sub-title>เบอร์โทรผู้ประสานงาน:{{ group.coordinatorTel }}</v-list-tile-sub-title>
              </v-list-tile-content>

              <!-- <v-list-tile-action>
                <v-btn icon ripple to="{name: 'EditGroupAdmin', params: {groupId: group.groupId}}">
                  <v-icon color="grey lighten-1">info</v-icon>
                </v-btn>
              </v-list-tile-action> -->

              <v-list-tile-action>
                <router-link :to="{name: 'edit-group', params: {groupName: group.groupName}}">
                  <v-icon color="grey lighten-1">edit</v-icon>
                </router-link>

              </v-list-tile-action>
            </v-list-tile>

          </v-list>
        </v-card>
      </v-flex>
    </v-layout>

    <!-- ปุ่มไปหน้า /RegisterGroup -->
    <router-link to="/">
      <v-btn fab dark color="red" class="v-btn v-btn--bottom v-btn--floating v-btn--fixed v-btn--right">
        <v-icon dark>add</v-icon>
      </v-btn>
    </router-link>


  </v-container>
</template>


<script>
  import firebaseApp from "@/components/firebaseInit";
  import SearchGroup from "@/views/SearchGroup";
  import * as firebase from 'firebase';
  
  export default {
    name: "group-list",
    components: { SearchGroup },
    data() {
      return {
        groups: [],
        groupsOnId: [],
        isLoading: false,
        model: null,
        search: ''
      };
    },
    // DB ต่อกับ collection ชื่อ yodByUser
    created() {
      firebaseApp.collection("yodByUser")
      //.doc('3WLksiIH82EFlzTMzKDw').collection('transactions')
        .orderBy('groupId','asc')
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            // console.log(doc.data());
            const data = {
              'id': doc.id,
              'groupId': doc.data().groupId,
              'groupName': doc.data().groupName,
              'groupMember': doc.data().groupMember,
              'coordinator': doc.data().coordinator,
              'coordinatorTel': doc.data().coordinatorTel,
              // 'create': doc.data().create,
              'yodSamma': doc.data().yodSamma
            };
            this.groups.push(data);
          });
            // const datasum = this.groups.length;
            // console.log(datasum);
        });
    },
    computed: {
      filteredGroup: function(){
        return this.groups.filter((group) => {
          return group.groupName.match(this.search);
        });
      }
    }
  };
</script>


